.index-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100%;
    max-width: 152px;
    padding: 4px;
    margin-right: 10px;
    position: relative;
    min-height: 468px; /* delete */
}

.index-container-updateDate {
    position: absolute;
    top: 50%;
    left: -81px;
    transform: translateY(-50%) rotate(270deg);
    font-family: "FiraSans-SemiBold";
    font-size: 3rem;
    letter-spacing: 0.8px;
}

.index-container-link {
    cursor: pointer;
}

.index-container-link span{
    font-family: "FiraSans-ExtraLight";
}

@media only screen and (max-width:1140px) {
    .index-container-updateDate {
        font-size: 2rem;
        left: -55px;
    }
}

@media only screen and (max-width:768px) {
    .index-container {
        order: 2;
        margin-right: 0;
        padding: 4px;
    }

    .index-container {
        max-width: none;
        min-height: auto;
    }
}