.summary-container {
    margin-top: 46px;
    margin-bottom: 55px;
}

.summary-heading {
    font-family: "FiraSans-Bold";
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 22px;
    line-height: 1;
}

.summary-content {
    font-size: 17px;
    line-height: 1.8;
    letter-spacing: 0.52px;
}

.summary-content strong {
    font-family: "FiraSans-Bold";
}

@media only screen and (max-width:768px) {
    .summary-container {
        margin-top: 40px;
        margin-bottom: 60px;
    }
}