.header {
    padding-top: 60px;
    display: flex;
    justify-content: space-between;
}

.header .main-heading {
    display: inline-block;
    font-family: "FiraSans-Light";
    font-size: 7.6rem;
    line-height: 1;
    margin-bottom: 8px;
    position: relative;
    letter-spacing: 0.9px;
}

.header .main-heading .bold {
    font-size: 7.5rem;
    font-family: "FiraSans-SemiBold";
    margin-left: 21px;
    letter-spacing: 1px;
}

.header .main-heading .main-heading-dot {
    display: inline-block;
    width: 12.5px;
    height: 12.5px;
    border-radius: 50%;
    margin-left: 5px;
    position: absolute;
    right: -15px;
    bottom: 10px;
}

.header .main-subheading {
    display: flex;
    justify-content: space-between;
    padding-right: 5px;
}

.header .main-subheading-info {
    font-family: "FiraSans-SemiBold";
    padding-left: 7px;
    font-size: 18px;
    letter-spacing: 0.1px;
}

.header .main-subheading-arabic {
    font-family: "AdobeNaskh-Medium";
    font-weight: normal;
    font-size: 2.2rem;
    letter-spacing: 0.2px;
}

.header__contact {
    padding-top: 22px;
    padding-right: 45px;
}

.header__contact--container {
    margin-bottom: 7px;
}

.header__contact h5 {
    font-size: 1.4rem;
    font-family: "FiraSans-SemiBold";
    letter-spacing: 1.7px;
    margin-bottom: 4px;
    line-height: 1;
}

.header__contact p {
    font-family: "FiraSans-ExtraLight";
    font-size: 1.4rem;
    letter-spacing: 0.5px;
}

@media only screen and (max-width:768px) {
    .header {
        flex-direction: column;
    }

    .header .main-heading {
        transform: translateX(-5px);
    }

    .header .main-subheading {
        justify-content: flex-start;
    }

    .header .main-subheading-info {
        margin-right: 40px;
    }

    .header__contact {
        display: flex;
    }

    .header__contact--container {
        margin-right: 25px;
    }
}

@media only screen and (max-width:651px) {
    .header {
        padding-top: 45px;
    }

    .header .main-heading {
        font-size: 6rem;
    }

    .header .main-heading .bold{
        font-size: 5.8rem;
    }

    .header .main-subheading-info {
        padding-left: 0;
    }

    .header .main-heading .main-heading-dot {
        width: 8px;
        height: 8px;
        bottom: 5px;
        right: -11px;
    }
}

@media only screen and (max-width:576px) {
    .header {
        padding-top: 30px;
    }

    .header .main-heading {
        font-size: 5rem;
    }

    .header .main-heading .bold{
        font-size: 4.7rem;
        margin-left: 12px;
    }

    .header .main-subheading-info {
        margin-right: 0;
    }
    
    .header .main-subheading {
        padding-right: 0;
    }

    .header .main-subheading {
        flex-direction: column;    
    }

    .header .main-subheading-arabic {
        font-size: 2rem;
    }
}

@media only screen and (max-width:401px) {
    .header .main-heading {
        font-size: 4.5rem;
    }

    .header .main-heading .bold{
        font-size: 4.15rem;
    }
}

@media only screen and (max-width:381px) {
    .header .main-heading {
        font-size: 4.15rem;
    }

    .header .main-heading .bold{
        font-size: 3.85rem;
        display: inline-block;
        margin-left: 0;
        position: relative;
    }
    
    .main-subheading-info {
        font-size: 17px;
    }
}