.reviews-container {
    padding-top: 75px;
    padding-bottom: 60px;
}

.reviews-main-heading {
    font-size: 3rem;
    letter-spacing: 0.9px;
    font-weight: bold;
    margin-bottom: 18px;
}

.review {
    display: flex;
    margin-bottom: 54px;
}

.review:last-child {
    margin-bottom: 0;
}

.review-icon {
    max-width: 125px;
    height: auto;
    box-shadow: 6px 6px 12px 1px rgba(0,0,0,0.6);
    border-radius: 25px;
    margin-right: 25px;
}

.review-content {
    padding-top: 12px;
}

.review-title {
    font-family: "FiraSans-Bold";
    font-size: 2.2rem;
    font-weight: bold;
    margin-bottom: 27px;
}

.review-description {
    font-size: 1.8rem;
    line-height: 1.8;
}

@media only screen and (max-width: 667px) {
    .review {
        flex-direction: column;
    }

    .review-icon {
        max-width: 75px;
        margin-bottom: 25px;
    }
}