*,*::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

@media (max-width:992px) {
  html {
    font-size: 9.5px;
  }
}

body {
  font-size: 1.6rem;
  overflow-x: hidden;
  background-color: #525659;
}

@media only screen and (max-width:576px) {
  body {
    overflow-wrap: break-word;
  }
}

#root {
  background-color: #fff;
  border-top: 1px solid #525659;
  max-width: 1000px;
  display: block;
  margin: 0 auto;
  transform: translateX(8px); /* for pdf, Delete */
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  max-width: 1000px;
  display: block;
  margin: 0 auto;
  padding: 0 46px;
}

