.cloud-container {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    padding: 35px 10px;
}

.tag-cloud-tag {
    margin: 0 35px !important;
    line-height: 1.75;
}

@media only screen and (max-width:768px) {
    .cloud-container {
        margin-bottom: 20px;
        padding: 15px 25px;
    }

    .tag-cloud-tag {
        margin: 0 12.5px !important;
        line-height: 1.3;
    }

    .tag-19 {
        font-size: calc(19px / 1.1875) !important;
    }

    .tag-28 {
        font-size: calc(28px / 1.1875) !important;
    }

    .tag-36 {
        font-size: calc(36px / 1.1875) !important;
    }

    .tag-45 {
        font-size: calc(45px / 1.1875) !important;
    }
}

@media only screen and (max-width:576px) {
    .tag-cloud-tag {
        margin: 4px 12.5px !important;
    }

    .tag-28 {
        font-size: calc(28px / 1.5) !important;
    }

    .tag-36 {
        font-size: calc(36px / 1.5) !important;
    }

    .tag-45 {
        font-size: calc(45px / 1.5) !important;
    }
}

