@media print {
    body {
        background-color: transparent !important;
    }

    /* Header */

    #main-content {
        min-height: calc(100vh);
        overflow: hidden;
        margin-bottom: 20px !important;
    }

    .header {
        padding-top: 10px !important;
    }

    .header .main-heading {
        font-size: 7rem !important;
        margin-bottom: 0 !important;
    }

    .main-subheading-info {
        font-size: 17px !important;
        padding-left: 0 !important;
    }

    .header__contact {
        padding-top: 10px !important;
        padding-right: 0 !important;
    }

    .indexCloud-container {
        margin-top: 10px !important;
    }

    .index-container {
        min-height: auto !important;
    }

    .cloud-container {
        padding: 15px 10px !important;
    }

    /* Skill cloud */
    

    .tag-cloud-tag {
        line-height: 1.3 !important;
    }

    .tag-19 {
        font-size: calc(19px / 1.1875) !important;
    }

    .tag-28 {
        font-size: calc(28px / 1.1875) !important;
    }

    .tag-36 {
        font-size: calc(36px / 1.1875) !important;
    }

    .tag-45 {
        font-size: calc(45px / 1.1875) !important;
    }

    .summary-container {
        margin-top: 10px !important;
        margin-bottom: 0 !important;
    }

    .summary-heading {
        font-size: 2.8rem !important;
        margin-bottom: 14px !important;
    }

    .summary-content {
        font-size: 15px !important; 
        line-height: 1.5 !important;
    }

    /* Projects */

    .project-container {
        max-height:calc(100vh);
        margin-bottom: 20px !important;
        border: none !important;
        overflow: hidden; /* delete */
    }

    .project-images{
        min-height: auto !important;
        height: 45vh;
        padding: 25px 0 !important;
    }

    .project-container-big {
        padding: 0 20px !important;
    }

    .project-images .container {
        max-height: 100%;
    }

    .project-images img {
        height: auto !important;
        width: auto !important;
        object-fit: contain;
    }

    .project-content {
        padding-top: 30px !important;
        height: 55vh;
        overflow: hidden;
    }

    .project__info--cta {
        margin-left: 15px;
    }

    .project-icon {
        max-width: 90px !important;
        margin-right: 20px !important;
    }

    .project-data {
        padding-top: 0 !important;
    }

    .project-title {
        font-size: 2.4rem !important;
        margin-bottom: 9px !important;
    }

    .project-description {
        font-size: 1.8rem !important;
        line-height: 1.4 !important;
    }

    .project__info {
        margin-bottom: 45px !important;
    }

    .project__about--tools {
        padding-left: 0 !important;
    }

    .project__about--heading {
        margin-bottom: 14px !important;
    }

    .project__about--heading img {
        max-height: 35px !important;
    }

    .project__about--heading h5 {
        font-size: 2.1rem !important;
    }

    .project__about--tools ul {
        line-height: 1.5 !important;
    }

    .project__about--divider {
        display: none !important;
    }

    .project__about--challenges {
        overflow: visible !important;
    }

    .project__about--challenges .project__about--heading {
        padding-left: 15px !important;
    }

    .project__about--challenges p {
        font-size: 16px !important;
        line-height: 1.5 !important;
    }

    .client-reviews {
        min-height: 100vh;
        margin-bottom: 20px;
    }

    .reviews-main-heading {
        font-size: 2.8rem;
        margin-bottom: 14px !important;
    }

    .review-icon {
        max-width: 90px !important;
        margin-right: 20px !important;
    }

    .review-content {
        padding-top: 10px !important;
    }

    .review-title {
        font-size: 2.1rem !important;
        margin-bottom: 21px !important;
    }

    .review-description {
        font-size: 1.6rem !important;
        line-height: 1.5 !important;
    }
}