.project-container {
    border-top: 20px solid #525659;
}

.project-container:last-of-type {
    border-bottom: 20px solid #525659;
}

.project-container-big {
    padding: 0 65px;
}

.project-images {
    background-color: #D1D5DB;
    padding: 50px 0;
    border-bottom: 2px solid #000;
    min-height: 685px;
}

.project-images .container {
    max-width: 820px;
    display: flex;
    justify-content: space-between;
}

.project-images img {
    height: 100%;
    max-height: 570px;
    width: auto;
}

.project-content {
    padding-top: 63px;
}

.project__info {
    margin-bottom: 83px;
    display: flex;
    justify-content: space-between;
}

.project__info--about {
    display: flex;
    align-items: flex-start;
    max-width: 625px;
}

.project-icon {
    max-width: 125px;
    height: auto;
    box-shadow: 6px 6px 12px 1px rgba(0,0,0,0.6);
    border-radius: 25px;
    margin-right: 23px;
}

.project-data {
    padding-top: 10px;
}

.project-title {
    font-size: 2.9rem;
    font-weight: bold;
    margin-bottom: 16px;
}

.project-description {
    font-size: 1.9rem;
    line-height: 1.55;
    letter-spacing: 0.32px;
}

.project__info--cta {
    display: flex;
    transform: translateY(-5px);
}

.project__info--cta img {
    max-width: 52px;
    height: auto;
    margin-right: 15px;
}

.project__info--cta a:last-child img{
    margin-right: 0;
}

.project__about {
    display: flex;
    justify-content: space-between;
    position: relative;
    margin-bottom: 90px;
}

.project__about--divider {
    content: "";
    position: absolute;
    left: 50%;
    top: 65px;
    transform: translateX(-50%);
    width: 6px;
    height: 110px;
    border-radius: 10px;
}

.project__about--heading {
    display: flex;
    align-items: center;
    margin-bottom: 22px;
}

.project__about--heading img {
    max-height: 40px;
    width: auto;
    margin-right: 14px;
}

.project__about--heading h5 {
    font-size: 2.3rem;
    font-family: "FiraSans-Medium";
    letter-spacing: 3px;
}

.project__about--tools {
    padding-left: 18px;
}

.project__about--tools ul {
    min-width: 388px;
    line-height: 1.95;
    columns: 2;
    padding-left: 16px;
}

.project__about--tools ul li {
    position: relative;
    max-width: 150px;
    letter-spacing: 0.6px;
}

.project__about--tools ul li::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 3px;
    height: 3px;
    border-radius: 50%;
}

.project__about--challenges {
    max-width: 400px;
}

.project__about--challenges p {
    padding-left: 15px;
    font-size: 17px;
    letter-spacing: 0.7px;
    line-height: 1.85;
}

@media only screen and (max-width:1024px) {
    /* .project-images img {
        max-height: 300px;
    } */
}

@media only screen and (max-width:992px) {

    .project__about--challenges {
        max-width: 340px;
    }

    .project__about--divider {
        display: none;
    }
}

@media only screen and (max-width:900px) {
    .project-images .container {
        justify-content: center;
    }

    .project-images img:first-of-type {
        margin-right: 50px;
    }
}

@media only screen and (max-width:851px) {
    .project__about {
        flex-direction: column;
    }

    .project__about--tools {
        margin-bottom: 60px;
    }

    .project__about--tools {
        margin-bottom: 0;
        padding-bottom: 30px;
        border-bottom: 3px solid rgb(210, 216, 214);
    }

    .project__about--challenges {
        max-width: 440px;
        padding-top: 30px;
    }

    .project__about--tools {
        padding-left: 0;
        padding-bottom: 15px;
    }

    .project__about--challenges p {
        padding-left: 0;
    }
}

@media only screen and (max-width:768px) {
    .project-images {
        min-height: auto;
    }

    .project-images img{
        max-height: 300px;
    }

    .project-content {
        padding: 60px 0;
    }

    .project__info {
        flex-direction: column;
        margin-bottom: 40px;
        position: relative;
    }

    .project__info--about {
        margin-bottom: 25px;
    }

    .project-data {
        padding-top: 15px;
    }

    .project__info--cta {
        position: absolute;
        right: 0;
        top: 0;
    }

    .project__info--cta img {
        margin-right: 25px;
        max-width: 40px;
    }

    .project__about--tools ul li {
        max-width: none;
    }
}

@media only screen and (max-width: 667px) {
    .project-icon {
        max-width: 75px;
    }

    .project-title {
        font-size: 1.95rem;
    }

    .project-description {
        font-size: 1.55rem;
    }
}

@media only screen and (max-width:576px) {
    .project-images img {
        max-height: 200px;
    }

    .project__info--cta {
        top: -26px;
    }

    .project__info--cta img {
        max-width: 32px;
    }

    /* .project__info--about {
        flex-direction: column;
    } */

    .project-icon {
        margin-bottom: 25px;
    }

    .project__about--tools ul li {
        margin-right: 12.5px;
    }
}

@media only screen and (max-width:501px) {
    .project__info--about {
        flex-direction: column;
    }

    .project__info--cta {
        top: 0;
        transform: translateY(0);
    }

    .project__about--tools ul {
        columns: 1;
    }

    .project__about--divider {
        display: none;
    }

}